const defaultPageRef = document.querySelector('.default-read');
const readConPageRef = document.querySelector('.revision-date');

let arrayOfCardsSelectedByReadMoreLink =
  JSON.parse(localStorage.getItem('readMore')) || [];

chectPage(arrayOfCardsSelectedByReadMoreLink);

function chectPage(local) {
  local.length === 0
    ? readConPageRef.classList.add('dis-none')
    : defaultPageRef.classList.add('dis-none');
}
